.clearfix::before,.clearfix::after{
    content:'';
    display:table;
    clear:both;
}

body{
    font: 14px/1.5 Helvetica Neue,Helvetica,Arial,Microsoft Yahei,Hiragino Sans GB,Heiti SC,WenQuanYi Micro Hei,sans-serif;
    color: #333;
    background-color: #fff;
    /* 不让body过小导致容器溢出 */
    min-width: 1226px;
}

/* 设置一个类，用来表示中间容器的宽度 */
.w{
    width:1226px;
    margin:0 auto;
}